// Get activities by category
export const getActivitiesByCategory = (category: Activity['category']) =>
  activities.filter(a => a.category === category);

// Get activities by city
export const getActivitiesByCity = (city: Activity['city']) =>
  activities.filter(a => a.city === city);

// Get activities by date
export const getActivitiesByDate = (date: string) =>
  activities.filter(a => a.date === date);

// Get must-do activities
export const getMustDoActivities = () =>
  activities.filter(a => a.priority === 'must-do');
